import { create } from 'zustand';
import { isEqual } from 'lodash-es';

const useDragStore = create<any>(set => ({
  isDragging: false,
  dragItem: null,
  coordi: {  },
  source: '',
  markDragging: (dragItem) => set({ isDragging: true, dragItem }),
  markDragEnd: () => set({ isDragging: false, dragItem: null, coordi: { }, source: '' }),
  setCoordi: (coordi) => set(state => {
    // const oldCoordi = state.coordi;
    // if (isEqual(oldCoordi, coordi)) {
    //   return state;
    // }
    return { coordi };
  })
}));

export default useDragStore;