import { useEffect } from "react";
import { useState } from "react";

const useCanvasRect = () => {

  const [layout, setLayout] = useState<DOMRect>();

  useEffect(() => {
    const canvasContainerInner = document.getElementById('canvas-container-inner');
    const canvasRootElement = document.getElementById('canvas-root');
    if (!canvasContainerInner || !canvasRootElement) {
      return;
    }
    setLayout(canvasRootElement.getBoundingClientRect()!);

    const observer = new ResizeObserver(entries => {
      for (const entry of entries) {
        if (entry.target.id = 'canvas-container-inner') {
          const canvasRootElement = document.getElementById('canvas-root');
          if (!canvasRootElement) {
            return;
          }
          const canvasRootRect = canvasRootElement.getBoundingClientRect();
          setLayout(canvasRootRect);
        }
      }
    });

    observer.observe(canvasContainerInner);

    return () => {
      observer.disconnect();
    }
  }, []);

  return layout;
}

export default useCanvasRect;