import React, { useState } from "react";
import useComponentStore from "../components/store";
import { useDrag } from 'react-dnd';
import styles from './index.css';
import { useEffect } from "react";
import { getEmptyImage } from 'react-dnd-html5-backend';
import useDragStore from "../drag/dragStore";
import { DragItemType, DragSourceType, GRID_ROW_HEIGHT } from "../constant";
import { DragSourceMonitor } from 'react-dnd';
import { generateInitialState, getGridItemWidth, getOffsetInRoot } from "../misc";

const PreviewComponent = ({componentInfo}) => {
  const { RawComponent, schema } = componentInfo;
  const previewProps = generateInitialState(schema);
  const { layout } = schema;
  const aspectRatio = layout.aspectRatio || 2;
  const height = 40;
  const width = height * aspectRatio;
  return (
    <div
      className={styles['preview-container']}
      style={{
        width,
        height,
      }}
    >
      <RawComponent {...previewProps} />
    </div>
  );
}

const ComponentCard = (props) => {
  const { componentInfo } = props;
  const schema = componentInfo.schema;
  const { type, cover, name, layout } = schema;
  const markDragging = useDragStore(state => state.markDragging);
  const markDragEnd = useDragStore(state => state.markDragEnd);

  const [{ isDragging }, drag, preview] = useDrag(() => ({
    type: DragItemType.COMPONENT,
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
      cursorOffset: monitor.getInitialClientOffset
    }),
    item: (monitor: DragSourceMonitor) => {
      const dragItem = { type, name, itemType: DragItemType.COMPONENT, layout, source: DragSourceType.COMPONENT_PANEL, schema };
      markDragging({ ...dragItem });
      return dragItem;
    },
    end(draggedItem, monitor) {
      markDragEnd();
    },
  }), [schema, markDragging]);

  useEffect(() => {
    const emptyImage = getEmptyImage();
    emptyImage.style.cursor = 'grabbing';
    preview(emptyImage, { captureDraggingState: false });
  }, [isDragging]);

  return (
    <div ref={drag} style={{ opacity: isDragging ? '0.5' : '1', cursor: isDragging ? 'grabbing' : 'grab' }} className={styles['component-card']}>
      <div className={styles['component-card-thumbnail']}>
        <PreviewComponent componentInfo={componentInfo} />
      </div>
      <div className={styles['component-card-name']}>{name}</div>
    </div>
  )
}

const ComponentsPanel = () => {
  const components = useComponentStore();
  const actualComponents = {};

  Object.keys(components).forEach(type => {
    const componentInfo = components[type];
    if (typeof componentInfo === 'function') {
      return;
    }
    actualComponents[type] = componentInfo;
  });

  return (
    <div className={styles['components-panel']}>
      {Object.entries(actualComponents).map(([type, componentInfo]) => (<ComponentCard key={type} componentInfo={componentInfo} />))}
    </div>
  )
}

export default ComponentsPanel;