import { Box, Typography } from "@mui/material";
import React, { FC } from "react";
import { navigations, useSideNavigatorStore } from "./sideNavigator";
import { Close as CloseIcon } from '@mui/icons-material'


const ToolHeader: FC = () => {
  
  const activedToolId = useSideNavigatorStore(state => state.actived);
  const setActived = useSideNavigatorStore(state => state.setActived);
  const activedToolInfo = navigations.find(({id}) => id === activedToolId);

  return (
    <Box display={'flex'} alignItems={'center'} height={30} padding={'0 12px'}>
      <Typography fontSize={13} variant="subtitle2" flex={1}>{activedToolInfo?.name || ''}</Typography>
      <CloseIcon sx={{ fontSize: '14px', cursor: 'pointer' }} onClick={() => setActived('')} />
    </Box>
  )
}

export default ToolHeader;