import { ReactNode } from "react";
import { useFormik } from 'formik';

interface CanvasPlugin {
  render: () => ReactNode;
}

interface Event {
  type: string;
  action: Action;
}

interface Action {
  type: string;
  options: any;
  advanced?: any;
}

interface MemoryAction extends Action {
  callback: Function;
}

export enum FormItemType {
  SELECT = 'select',
  INPUT = 'input',
  SWITCH = 'switch',
  EDITOR = 'editor',
}

interface FormItem {
  type?: FormItemType,
  field?: string;
  label?: string;
  required?: boolean;
  render?: (formItem: FormItem, formik: ReturnType<typeof useFormik>) => ReactNode;
  defaultValue?: any;
  [index: string]: any;
}

interface ControlComponentContext {
  target: string;
  source: string;
  self: string;
  eventId: string;
  eventType: string;
  options: any;
  api: any;
}

interface ComponentSchema {
  type: string;
  name: string;
  attributes: ComponentAttribute[];
  events?: ComponentEvent[];
  methods?: ComponentMethod[];
  layout: ComponentLayout;
  cover?: string;
}

interface ComponentAttribute {
  key: string;
  widget: string;
  name: string;
  defaultValue: any;
  widgetProps?: any;
}

interface ComponentEvent {
  type: string;
  name: string;
  bindState?: string;
}

interface ComponentMethod {
  type: string;
  name: string;
  optionSchema?: FormItem[];
  handler: (context: ControlComponentContext) => void;
}

interface ComponentLayout {
  width: number;
  height: number;
  minWidth?: number;
  minHeight?: number;
  // 
  aspectRatio?: number;
}

// 
interface ExpressionCalledSource {
  type: 'component' | string;
  id: string;
}