import { Box, styled } from "@mui/material";
import React, { FC, useReducer } from "react";
import { RichTreeView, TreeItem2, TreeItem2Props, treeItemClasses } from '@mui/x-tree-view';
import { mapSchema, useSchemaStore } from "../schema";
import { useShallow } from "zustand/react/shallow";
import { useMemo } from "react";
import { deepMap } from "../misc";
import useCanvasStore from "../canvas/store";
import { compact, uniq } from "lodash-es";
import { useState } from "react";
import { useEffect } from "react";
import { alpha } from "@mui/material";


let _forceUpdate;
export function forceUpdateComponentTree() {
  _forceUpdate?.();
}

const StyledTreeItem = styled(TreeItem2)(({ theme }) => {
  return {
    [`& .${treeItemClasses.content}`]: {
      borderRadius: theme.spacing(0.5),
      padding: theme.spacing(0.5, 1),
      margin: theme.spacing(0.2, 0),
      [`& .${treeItemClasses.label}`]: {
        fontSize: '0.8rem',
        fontWeight: 500,
      },
    },
    [`& .${treeItemClasses.iconContainer}`]: {
      borderRadius: '50%',
      backgroundColor: theme.palette.primary.dark,
      padding: theme.spacing(0, 1.2),
      ...theme.applyStyles('light', {
        backgroundColor: alpha(theme.palette.primary.main, 0.25),
      }),
      ...theme.applyStyles('dark', {
        color: theme.palette.primary.contrastText,
      }),
    },
    [`& .${treeItemClasses.groupTransition}`]: {
      marginLeft: 16,
      paddingLeft: 10,
      borderLeft: `1px dashed ${alpha(theme.palette.text.primary, 0.4)}`,
    },
    ...theme.applyStyles('light', {
      color: theme.palette.grey[800],
    }),
  }
});

interface ITreeItemProps extends TreeItem2Props {

}

const CustomTreeItem: FC<TreeItem2Props> = React.forwardRef((props, ref: React.Ref<HTMLLIElement>) => {
  const setHovered = useCanvasStore(state => state.setHovered);
  return <StyledTreeItem 
    {...props} 
    ref={ref}
    slotProps={{
      root: {
        onMouseOverCapture: () => {
          setHovered(props.itemId);
        }
      }
    }}
  />
});

interface IProps {

}

const ComponentTree: FC<IProps> = () => {

  const { schema, plainSchema } = useSchemaStore(useShallow(state => ({
    schema: state.schema,
    plainSchema: state.plainSchema,
  })));

  const { selected, setSelected, setHovered } = useCanvasStore(useShallow(state => ({
    selected: state.selected,
    setSelected: state.setSelected,
    setHovered: state.setHovered,
  })));

  const [_, forceUpdate] = useReducer(x => x + 1, 0);
  _forceUpdate = forceUpdate;



  const [items, plainItems] = useMemo(() => {
    const plainMappedSchema = {};
    const mappedSchema = mapSchema(schema, (node, path) => {
      const newNode = {
        ...node,
        label: node.id,
        path,
      };
      plainMappedSchema[node.id] = newNode;
      return newNode;
    });

    return [[mappedSchema], plainMappedSchema]
  }, [plainSchema, schema]);

  const [expandedIds, setExpandedIds] = useState<string[]>([]);

  useEffect(() => {
    const selectedItemNode = plainItems[selected];
    if (!selectedItemNode) {
      return;
    }
    setExpandedIds(uniq([...expandedIds, ...selectedItemNode.path, selected]));
  }, [selected]);;

  return (
    <Box>
        <RichTreeView 
          slots={{ item: CustomTreeItem }} 
          items={items} 
          selectedItems={selected}
          expandedItems={expandedIds}
          onExpandedItemsChange={(_, ids) => {
            setExpandedIds(ids);
          }}
          onSelectedItemsChange={(_, id) => { 
            setSelected(id);
          }}
          onMouseOut={() => setHovered('')}
        />
    </Box>
  )
}

export default ComponentTree;