import { CSSProperties } from "react";
import useCanvasRect from "./useCanvasLayout";
import { GRID_COLUMN_COUNT, GRID_ROW_HEIGHT } from "../constant";
import { useMemo } from "react";

const useGridStyle = (drawLines = true) => {

  const canvasRect = useCanvasRect();

  const gridBgStyles: CSSProperties = useMemo(() => {
    const styles: CSSProperties = {
      // backgroundImage: `
      // linear-gradient(to right, #ccc 1px, transparent 1px),
      // linear-gradient(to bottom, #ccc 1px, transparent 1px)`,
      backgroundSize: `${(canvasRect?.width || 0) / GRID_COLUMN_COUNT}px ${GRID_ROW_HEIGHT}px`,
      display: 'grid',
      gridTemplateRows: `repeat(auto-fill, ${GRID_ROW_HEIGHT}px)`,
      gridTemplateColumns: `repeat(${GRID_COLUMN_COUNT}, 1fr)`,
    };
    if (!drawLines) {
      delete styles.backgroundImage;
      delete styles.backgroundSize;
    }
    return styles;
  }, [canvasRect?.width, drawLines]) ;

  return gridBgStyles;
}

export default useGridStyle;

