function FormLabel({ content, width = 50 }) {
  return (
    <Typography variant='subtitle1' color='textSecondary' sx={{ lineHeight: 'unset',width, whiteSpace: 'nowrap', textOverflow: 'ellipsis', overflow: 'hidden' }}>
      {content}
    </Typography>
  )
}

function CommonLabel(props) {
  return (
    <div style={{ display: 'flex', alignItems: 'center', columnGap: 10 }}>
      <Typography fontSize={12} width={50} whiteSpace={'nowrap'} overflow={'hidden'} textOverflow={'ellipsis'}>{props.label}</Typography>
      <div style={{ flex: '1 1' }}>
        {props.children}
      </div>
    </div>
  )
}

function renderFormItem(item, formik, handleChange) {
  const { type, field, label, options, labelId, size = 'small', hidden, render, onChange } = item;
  if (hidden) {
    return null;
  }
  if (typeof render === 'function') {
    return render(item, formik);
  }
  let formItem: any = null;
  let commonProps: any = {
    id: field,
    name: field,
    value: get(formik.values, field, ''),
    onChange: (e) => {
      handleChange(e);
      onChange?.(e, formik);
    },
    size,
  }
  switch(type as FormItemType) {
    case FormItemType.INPUT:
      formItem = (
        // <FormControl fullWidth>
        //   <TextField 
        //     {...commonProps}
        //   />
        // </FormControl>
        <CommonLabel label={label}>
           <TextField 
            {...commonProps}
            hiddenLabel
            fullWidth
          />
        </CommonLabel>
      )
      break;
    case FormItemType.SELECT:
      const children = map(options, ({value, label}) => <MenuItem key={value} value={value}>{label}</MenuItem>)
      commonProps.children = children;
      // commonProps.labelId = labelId;
      formItem = (
        // <FormControl fullWidth>
        //   <InputLabel size={size} id={labelId}>{item.label}</InputLabel>
        //   <Select
        //     {...commonProps}
        //     MenuProps={{
        //       sx: {
        //         '& .MuiMenuItem-root': {
        //           fontSize: '12px'
        //         }
        //       }
        //     }}
        //   />
        // </FormControl>
        <CommonLabel label={label}>
          <Select
            style={{ height: '30px', fontSize: '11px' }}
            {...commonProps}
            hiddenLabel
            fullWidth
            MenuProps={{
              sx: {
                '& .MuiMenuItem-root': {
                  fontSize: '12px'
                }
              }
            }}
          />
        </CommonLabel>
      )
      break;
    case FormItemType.SWITCH:
      formItem = (
        <CommonLabel label={label}>
          <Switch {...commonProps}  />
        </CommonLabel>
      )
      break;
    case FormItemType.EDITOR:
      formItem = (
        <CommonLabel label={label}>
          <CodeEditor {...commonProps} hideLineNo />
        </CommonLabel>
      )
    default:
      break;
  }

  return formItem;
}

//  compileSchema 
const functionKeys = [
  'render',
  'onChange',
];

//  schema ,
function compileSchema(raw, values) {
  const sandbox = new FunctionSandBox({
    mode: 'expression',
  });
  const context = {
    formValues: values,
  };
  sandbox.init(context);
  
  const expressionRegExp = /\{\{([\s\S]*?)\}\}/ig;

  const result = deepMap(raw, (value: any, key) => {
    if (typeof value === 'function' && !functionKeys.includes(key)) {
      return value(values);
    }
    if (typeof value !== 'string') {
      return value;
    }
    const trimValue = value.trim();
    let match: RegExpExecArray | null = null;
    let returnValue = trimValue;
    const expressionRecords: any[] = [];
    while((match = expressionRegExp.exec(trimValue)) !== null) {
      const expression = match[1];
      const expressionWithBracket = match[0];
      const expressionReturn = sandbox.exec(expression);
      if (expressionWithBracket.length === trimValue.length) {
        returnValue = expressionReturn;
        break;
      }
      expressionRecords.push({
        expression,
        expressionWithBracket,
        expressionReturn,
        index: match.index,
      });
    }
    if (expressionRecords.length) {
      expressionRecords.reverse().forEach(record => {
        const prefixStr = returnValue.substring(0, record.index);
        const sufixStr = returnValue.substring(record.index + record.expressionWithBracket.length, returnValue.length);
        returnValue = prefixStr + record.expressionReturn + sufixStr;
      });
    }
    expressionRegExp.lastIndex = 0;
    return returnValue;
  });

  result.items = result.items.flat();
  return result;
}

interface IProps {
  schema: any;
  initialValues: any;
  onChange?: any;
  onSubmit?: any;
  size?: 'small' | 'medium';
  enableExpression?: boolean;
}

export const FormRender = ({ schema, initialValues, onChange, onSubmit, size, enableExpression = true }: IProps)  => {
  
  const formik = useFormik({
    initialValues,
    onSubmit(values, formikHelpers) {
      onSubmit?.(values, formikHelpers);
    },
  });

  const currentFormikRef = useRef(formik);
  currentFormikRef.current = formik;

  const handleChange = useCallback((e) => {
    formik.handleChange(e);
    runAsync(() => {
      //  formik onChange
      onChange?.({ ...currentFormikRef.current.values });
    });
  }, [onChange, formik, formik.handleChange]);

  const finalSchema = useMemo(() => {
    if (!enableExpression) {
      return schema;
    }
    return compileSchema(schema, currentFormikRef.current.values);
  }, [schema, enableExpression]);
  
  useMemo(() => {
    // const values: any = {};
    // finalSchema.items.forEach(item => {
    //   if (!item.field) {
    //     return;
    //   }
    //   const initalValue = get(initialValues, item.field);
    //   const defaultValue = item.defaultValue;
    //   set(values, item.field, initalValue ?? defaultValue);
    // })
    // console.log('values', values);
    currentFormikRef.current.setValues(initialValues);
  }, [initialValues]);

  return (
    <div>
      <form onSubmit={formik.handleSubmit}>
        {map(finalSchema?.items, item => {
          const labelId = `${item.field}-label`;
          const itemStyle: CSSProperties = {};
          return (
            <div className={styles['form-item']} key={item.field || item.key}>
              {renderFormItem({ ...item, labelId, size }, formik, handleChange)}
            </div>
          )
        })}
      </form>
    </div>
  )
}