import { Toolbar, Divider, List, ListItem, ListItemButton, ListItemIcon, ListItemText, Typography } from "@mui/material";
import { Box, Drawer } from "@mui/material";
import React from "react";
import { useState } from "react";
import { FC } from "react";
import AddUIIcon from '@mui/icons-material/AddCircleOutlineOutlined';
import ComponentTreeIcon from '@mui/icons-material/AccountTreeOutlined';
import OpenNavIcon from '@mui/icons-material/KeyboardDoubleArrowRight';
import CloseNavIcon from '@mui/icons-material/KeyboardDoubleArrowLeft';
import { useMemo } from "react";
import { create } from "zustand";
import { useShallow } from "zustand/react/shallow";
import ComponentsPanel from "../componentsPanel";
import ComponentTree from "./componentTree";

export const useSideNavigatorStore = create<any>((set, get) => ({
  actived: 'addUI',
  open: false,
  setActived: actived => set({ actived }),
  setOpen: open => set({ open }),
}));

export const navigations = [
  {
    id: 'addUI',
    name: '',
    icon: <AddUIIcon />,
    component: ComponentsPanel,
  },
  {
    id: 'componentTree',
    name: '',
    icon: <ComponentTreeIcon />,
    component: ComponentTree,
  }
];

export const getToolComponent = (toolId) => {
  return navigations.find(nav => nav.id === toolId)?.component || (() => null);
}

const SideNavigator: FC = () => {

  const { actived, open, setOpen, setActived } = useSideNavigatorStore(useShallow(state => ({
    actived: state.actived,
    open: state.open,
    setOpen: state.setOpen,
    setActived: state.setActived,
  })));

  const drawerWidth = useMemo(() => {
    if (open) {
      return 160;
    }
    return 60;
  }, [open]);

  return (
    <Drawer
        onClose={() => setOpen(false)}
        open={open}
        sx={{
          width: drawerWidth,
          overflow: 'hidden',
          transition: 'width .3s',
          flexShrink: 0,
          '& .MuiDrawer-paper': {
            transition: 'width .3s',
            width: drawerWidth,
            boxSizing: 'border-box',
            overflow: 'hidden',
          },
        }}
        variant="permanent"
        anchor="left"
      >
        <Toolbar sx={{ minHeight: '40px !important' }} />
        {/* <Divider /> */}
        <List>
          <ListItem>
            <ListItemIcon
              sx={{ cursor: 'pointer' }}
              onClick={() => {
                setOpen(!open);
              }}
            >
              {open ? <CloseNavIcon /> : <OpenNavIcon />}
            </ListItemIcon>
          </ListItem>
          <Divider sx={{ marginBottom: '1px' }} />
          {navigations.map(({ name, id, icon }, index) => (
            <ListItem key={id} disablePadding sx={{ whiteSpace: 'nowrap' }}>
              <ListItemButton
                onClick={() => setActived(id)}
                style={{ backgroundColor: actived === id ? 'var(--container-bg-hover-1)' : '' }}
              >
                <ListItemIcon>
                  {icon}
                </ListItemIcon>
                <ListItemText primary={<Typography fontSize={12}>{name}</Typography>} />
              </ListItemButton>
            </ListItem>
          ))}
        </List>
      </Drawer>
  );
}

export default SideNavigator