import React from 'react';
import styles from './index.css';
import ComponentsPanel from './componentsPanel';
import Canvas from './canvas';
import PropsPanel from './propsPanel';
import DargLayer from './drag/dragLayer';import { Box, Divider, Fab, Typography } from '@mui/material';
import SideNavigator, { getToolComponent, useSideNavigatorStore } from './tools/sideNavigator';
import ToolHeader from './tools/toolHeader';
import { create } from 'zustand';
import { ListSharp } from '@mui/icons-material';

export const useLayoutStore = create<any>((set, get) => ({
  propsPanel: {
    visible: true,
  },
  setPropsPanelVisible: visible => set(state => {
    state.propsPanel.visible = visible;
    return { ...state };
  }),
}));

const Editor = () => {

  const activedToolId = useSideNavigatorStore(state => state.actived);
  const ActivedTool = getToolComponent(activedToolId);
  const propsPanelVisible = useLayoutStore(state => state.propsPanel.visible);
  const setPropsPanelVisible = useLayoutStore(state => state.setPropsPanelVisible);

  return (
    <div>
      <div id="low-code-container" className={styles['low-code-container']}>
        <SideNavigator />
        {activedToolId &&  <div className={styles['left-side-tool-container']}>
          <ToolHeader />
          <Divider />
          <ActivedTool />
        </div>}
        <div className={styles['canvas-container']} id="canvas-container">
          <Canvas />
        </div>
        {propsPanelVisible && <div style={{ flex: '0 0 250px' }}>
          <PropsPanel />
        </div>}
        <DargLayer />
      </div>
      {!propsPanelVisible && <Fab
        variant='extended'
        size='small'
        onClick={() => setPropsPanelVisible(true)}
        sx={{
          position: 'fixed',
          right: -55,
          top: 70,
          borderRadius: '5px',
          transition: 'transform .3s',
          transform: `translateX(0)`,
          '&:hover': {
            transform: 'translateX(-50px)'
          }
        }}
      >
        <ListSharp sx={{ fontSize: '16px', mr: 1 }} />
        <Typography fontSize={11}></Typography>
      </Fab>}
    </div>
  );
}

export default Editor;