import { Box, Typography, Divider } from "@mui/material";
import { title } from "process";
import React from "react";
import { ReactEventHandler } from "react";
import { useMemo } from "react";
import { FC } from "react";

export interface IProps {
  titles?: string[];
  description?: string;
  example?: string;
  onClick?: ReactEventHandler;
  onMouseDown?: ReactEventHandler;
}

const CodeEditorTip: FC<IProps> = (props) => {

  const { titles, description, example, onClick, onMouseDown } = props;

  const showDivider = titles && ( description || example );

  return (
    <Box
      onClick={onClick}
      onMouseDown={onMouseDown}
      sx={{
        minWidth: '50px',
        backgroundColor: 'var(--container-bg-4)'
      }}
    >
      <Box
        sx={{
          p: '5px 10px',
          display: 'flex'
        }}
      >
        {titles && titles.map((title,index) => {
          return (
            <Box display={'flex'} alignItems={'center'} key={title}>
              <Typography variant="button" fontSize={'12px'}>{title}</Typography>
              {index < titles.length - 1 && <Divider orientation="vertical" sx={{ m: '0 10px' }} />}
            </Box>
          )
        })}
      </Box>
      {showDivider && <Divider />}
      {description && <Box
        sx={{
          p: '5px 10px',
          display: 'flex',
        }}
        >
        <Typography fontSize={'12px'}>{description}</Typography>
      </Box>}
      {example && <Box p='5px'>
        <Box>
          <Typography fontSize={12}></Typography>
        </Box>
        <Box>
          <Typography fontSize={11}>{example}</Typography>
        </Box>
      </Box>}
    </Box>
  )
}

export default CodeEditorTip;