import useDragStore from "../drag/dragStore";
import { hasCoincidence } from "../misc";
import { useSchemaStore } from "../schema";
import useCanvasStore from "./store";


/**
 * resize
 */
export const shouldRestrict = (nextLayout) => {
  const { isDragging, dragItem } = useDragStore.getState();
  const { isResizing, resizeItem } = useCanvasStore.getState();
  if (!isDragging && !isResizing) {
    return false;
  }
  const { schema } = useSchemaStore.getState();
  const firstClassChildren = schema.children || [];
  for (const child of firstClassChildren) {
    if (child.id === dragItem?.id || child.id === resizeItem?.id) {
      continue;
    }
    if (hasCoincidence(nextLayout, child.layout)) {
      return true;
    }
  }
  return false;
}