export const generateComponentId = (type: string) => {
  const plainSchema = useSchemaStore.getState().plainSchema;
  const allNodes = Object.values(plainSchema);
  const specNodes = allNodes.filter((node: any) => node.type === type);
  let suffixIds: any[] = [];
  const suffixRegExp = new RegExp(`^${type}(\\d+)$`)
  specNodes.forEach((node: any) => {
    const matches = suffixRegExp.exec(node.id);
    if (!matches) {
      return;
    }
    suffixIds.push(Number(matches[1]));
  });

  if (!suffixIds.length) {
    return `${type}1`;
  }

  suffixIds = suffixIds.sort((a, b) => a - b);

  let i = 0;
  for (; i < suffixIds.length; i++) {
    if (i + 1 < suffixIds[i]) {
      break;
    }
  }
  return `${type}${i + 1}`;
}

export const generateInitialState = (schema: any = {}) => {
  const { attributes } = schema;
  if (!Array.isArray(attributes)) {
    return {};
  }
  const initialState = {};
  attributes.forEach(attribute => {
    const { key, defaultValue } = attribute;
    initialState[key] = defaultValue;
  })
  return initialState;
}

export const getCanvasRootElement = () => {
  return document.getElementById('canvas-root');
}

export const getOffsetInRoot = ({ x, y }) => {
  const rootElement = getCanvasRootElement();
  if (!rootElement) {
    return { x, y };
  }
  const rootRect = rootElement.getBoundingClientRect();

  const relativeX = x - rootRect.x;
  const relativeY = y - rootRect.y;

  return { x: relativeX, y: relativeY };
}

export const getGridItemWidth = () => {
  const canvasRootElement = getCanvasRootElement();
  if (!canvasRootElement) {
    return 0;
  }
  return canvasRootElement.offsetWidth / GRID_COLUMN_COUNT;
}

export const transferCoordiToGrid = (coordi: any, gridItemWidth: number, gridItemHeight: number) => {
  return {
    x: Math.round(coordi.x/ gridItemWidth) + 1,
    y: Math.round(coordi.y/ gridItemHeight) + 1,
  }
}

export const transferLayoutToGridPoints = (layout) => {
  const { x, y, width, height } = layout;
  return {
    topLeft: { x, y },
    topRight: { x: x + width, y},
    bottomLeft: { x, y: y + height},
    bottomRight: { x: x + width, y: y + height },
  };
}

export const transferGridPointsToLayout = (points) => {
  const { topLeft, bottomRight, topRight, bottomLeft } = points;
  if (topLeft && bottomRight) {
    return {
      x: topLeft.x,
      y: topLeft.y,
      width: bottomRight.x - topLeft.x,
      height: bottomRight.y - topLeft.y,
    }
  }
  return {
    type: 'grid',
    x: bottomLeft.x,
    y: topRight.y,
    width: topRight.x - bottomLeft.x,
    height: bottomLeft.y - topRight.y,
  }
}

export const isInRect = (source, rect) => {
  const { x, y } = source;
  return (
    x > rect.x 
    && x < rect.x + rect.width
    && y > rect.y
    && y < rect.y + rect.height
  )
}

export const hasCoincidence = (layout1, layout2) => {
  const points1 = transferLayoutToGridPoints(layout1);
  const points2 = transferLayoutToGridPoints(layout2);

  const areaCoincidence = !(
    points1.topLeft.x >= points2.topRight.x
    || points1.topRight.x <= points2.topLeft.x
    || points1.topLeft.y >= points2.bottomLeft.y
    || points1.bottomLeft.y <= points2.topLeft.y
  );

  return areaCoincidence;
}

export const generateShortId = () => {
  return Math.random().toString(36).substring(2, 8);
}

export const now = () => {
  return dayjs().format(DATE_FORMAT);
}

export const isAfter = (date1, date2) => {
  return dayjs(date1).isAfter(dayjs(date2));
}

export const isBefore = (date1, date2) => {
  return dayjs(date1).isBefore(dayjs(date2));
}

export const isSame = (date1, date2) => {
  return dayjs(date1).isSame(dayjs(date2));
}

/**
 * mapper
 * @param obj 
 * @param mapper 
 * @returns 
 */
export function deepMap(obj, mapper, path: any[] = []) {
  const mapFn: any = Array.isArray(obj) ? map : mapValues;
  return mapFn(obj, (value, key) => {
    const newPath = [...path, key];
    if (isObject(value) && typeof value !== 'function') {
      return deepMap(value, mapper, newPath); 
    }
    return mapper(value, key, newPath);
  });
}

