import { CanvasPlugin } from "../../type";
import AdaptivePluagin from "./adaptive";
import TipLayerPlugin from "./tipLayer";
import GridLayerPlugin from "./gridLayer";

class PluginCenter {

  Fragments: any[] = [];

  use(plugin: CanvasPlugin) {
    this.Fragments.push(() => plugin.render.bind(plugin)());
  }
}

const pluginCenter = new PluginCenter();

pluginCenter.use(new AdaptivePluagin());
pluginCenter.use(new TipLayerPlugin());
pluginCenter.use(new GridLayerPlugin());

export default pluginCenter;