import { create } from 'zustand';
import { isSystemInDarkMode } from './misc';

const useGlobalStore = create<any>(set => ({
  mode: 'edit',
  userInfo: { username: '', uid: 9527, jwt: '' },
  setUser: userInfo => set({ userInfo }),
  theme: localStorage.getItem('theme') || (isSystemInDarkMode() ? 'dark' : 'light'),
  setMode: mode => set({ mode }),
  setTheme: theme => set({ theme }),
  toggleTheme: () => set(state => {
    const { theme } = state;
    if (theme === 'light') {
      return {
        theme: 'dark',
      }
    }
    if (theme === 'dark') {
      return {
        theme: 'light',
      }
    }
  }),
}))

export default useGlobalStore;