import React from 'react';
import { useSchemaStore } from '../schema';
import { travisRender } from './render';
import { useDrop } from 'react-dnd';
import { DragItemType, DragSourceType, DragTargetType, EventType, GRID_ROW_HEIGHT, LayoutType } from '../constant';
import styles from './index.css';
import useComponentStore from '../components/store';
import { pickBy } from 'lodash-es';
import { generateComponentId, generateInitialState, getGridItemWidth, getOffsetInRoot } from '../misc';
import { useStatesStore } from '../states';
import useDragStore from '../drag/dragStore';
import evtManager from '../event';
import pluginCenter from './plugins';
import useGlobalStore from '../globalStore';
import useCanvasStore from './store';
import { shouldRestrict } from './utils';
import { mountMethods } from '../action/methods';

const Canvas = (props) => {
  const schema = useSchemaStore(state => state.schema);
  const setCoordi = useDragStore(state => state.setCoordi);
  // const dragCoordi = useDragStore(useShallow(state => state.coordi));
  const mode = useGlobalStore(state => state.mode);
  const inserting = useCanvasStore(state => state.inserting);
  useDragStore(state => state.isDragging);
  useSchemaStore(state => state.plainSchema);

  const [, drop] = useDrop(() => ({
    accept: DragItemType.COMPONENT,
    hover(item: any, monitor) {
      const initialOffset = monitor.getInitialClientOffset();
      const sourceOffset = monitor.getInitialSourceClientOffset();
      const canvasContainerInnerElement = document.getElementById('canvas-container-inner');
      if (!initialOffset || !sourceOffset || !canvasContainerInnerElement) {
        return;
      }
      // 
      const relativeX = initialOffset.x - sourceOffset.x;
      const relativeY = initialOffset.y - sourceOffset.y;
      const clientOffset = monitor.getClientOffset();

      if (!clientOffset) {
        return;
      }
      const gridItemWidth = getGridItemWidth();
      const coordi = getOffsetInRoot({ x: Math.round(clientOffset.x - relativeX), y: Math.round(clientOffset.y - relativeY)  });
      const finalCoordi = {
        x: Math.min(Math.max(coordi.x, 0), canvasContainerInnerElement.offsetWidth - item.layout.width * gridItemWidth), 
        y: Math.max(coordi.y, 0)
      };

      setCoordi(finalCoordi);
      evtManager.emit(EventType.DRAGGING, { item, coordi, clientOffset });
      // 
      let grid = { 
        x: Math.round(finalCoordi.x / gridItemWidth) + 1,
        y: Math.round(finalCoordi.y / GRID_ROW_HEIGHT) + 1
      };
      const nextLayout = {
        ...item.layout,
        ...grid,
      };
      if (shouldRestrict(nextLayout)) {
        return;
      }
      useCanvasStore.getState().setPreviewLayout(nextLayout);
    },
    canDrop(item, monitor) {
      return !inserting
    },
    drop: (item: any, monitor) => {
      if (monitor.getItemType() !== DragItemType.COMPONENT){
        console.error('wrong type', item);
      }
      if (!item.type) {
        console.error('dropItem type required');
        return;
      }

      const grid = useCanvasStore.getState().previewLayout;

      if (item.source === DragSourceType.CANVAS) {
        const { updateNode, moveChild } = useSchemaStore.getState();
        if (item.layout?.inSlot) {
          moveChild(item.id, 'CanvasRoot');
        }
        updateNode(item.id, { layout: { ...grid, inSlot: '' } });
      }

      if (item.source === DragSourceType.COMPONENT_PANEL) {
        const components = useComponentStore.getState();
        const setState = useStatesStore.getState().setState;
        const appendChild = useSchemaStore.getState().appendChild;
  
        const actualComponents = pickBy(components, component => typeof component !== 'function');
        const componentInfo = actualComponents[item.type];
        if (!componentInfo) {
          console.error('component not found', item);
        }
  
        const componentSchema = componentInfo.schema;
        const initialState = generateInitialState(componentSchema);
        const id = generateComponentId(componentInfo.type);
        item.id = id;

        const newSchemaNode = {
          id,
          type: componentInfo.type,
          state: initialState,
          layout: {
            type: LayoutType.GRID,
            ...grid,
            width: componentSchema.layout.width,
            height: componentSchema.layout.height,
          }
        };
  
        setState(id, initialState);
        appendChild('CanvasRoot', newSchemaNode);
        mountMethods(newSchemaNode.id);
      }
      evtManager.emit(EventType.DROP, { item: { ...item, target: DragTargetType.CANVAS } });
    },
  }), [inserting]);

  return (
    <>
      <div id='canvas-container-inner' className={styles['canvas-container-inner']} ref={drop}>
        {travisRender(schema)}
      </div>
      {mode === 'edit' ? pluginCenter.Fragments.map((Fragment, index) => <Fragment key={index} />) : null}
    </>
  )
}

export default Canvas;

