import { create } from 'zustand';
import { isNil, merge, cloneDeep, set, some, isEmpty } from 'lodash-es';
import { useStatesStore } from '../states';
import { deepMap, generateInitialState, runAsync } from '../misc';
import useComponentStore from '../components/store';
import { SlotType } from '../constant';
import { useActionStore } from '../action';
import { mountMethods, unmountMethods } from '../action/methods';
import useCanvasStore from '../canvas/store';
import { compileExpression, updateExpressionContext } from '../expression';

const getInitialSchema = () => ({
  type: 'CanvasRoot',
  id: 'CanvasRoot'
});

export const deps = {};

function resolveNodeDeps(node) {
  const expressionRegExp = /\{\{([\s\S]*?)\}\}/;
  const newState = {};
  deepMap(node.state, (value, key, path) => {
    if (typeof value !== 'string' || !expressionRegExp.test(value)) {
      return;
    }
    const [returnValue, fieldDeps] = compileExpression(value);
    // 
    const isNested = some(fieldDeps, dep => dep.id === node.id && dep.key === path.join('.'));
    if (isNested) {
      console.error('nested expression');
      return;
    }
    set(newState, path, returnValue);
    deps[[node.id, ...path].join('.')] = fieldDeps;
  });
  if (!isEmpty(newState)) {
    useStatesStore.getState().setState(node.id, newState);
  }
}


function travisSchema(node, callback, parent = null) {
  if (!node) {
    return;
  }
  callback(node, parent);
  if (Array.isArray(node.children)) {
    let index = 0;
    for(;index < node.children.length; index ++) {
      travisSchema(node.children[index], callback, node);
    }
  }
}

export function mapSchema(node, mapper, path: string[] = []) {
  const copiedNode = { ...node };
  const pathForChild = [...path, copiedNode.id];
  if (Array.isArray(copiedNode.children)) {
    copiedNode.children = [...copiedNode.children];
    for(let index = 0;index < copiedNode.children.length; index ++) {
      copiedNode.children[index] = mapSchema(copiedNode.children[index], mapper, pathForChild);
    }
  }
  return mapper(copiedNode, path);
}

const useSchemaStore = create<any>((set, get) => ({
  schema: null,
  plainSchema: null,
  getSerializedSchema: () => {
    const { schema } = get();
    const clonedSchema = cloneDeep(schema);
    travisSchema(clonedSchema, (node, parent) => {
      delete node.parent;
      if (node.events) {
        node.events.forEach(event => {
          event.action.callback = String(event.action.callback);
        });
      }
    })
    return clonedSchema;
  },
  initialSchema: schema => {
    const { setState, clearStates } = useStatesStore.getState();
    const componentInfos = useComponentStore.getState();
    const { addEvent } = useActionStore.getState();
    const plainSchema: any = {};
    clearStates();
    travisSchema(schema, (node, parent) => {
      // 
      mountMethods(node);
      setState([node.id], node.state || generateInitialState(componentInfos[node.type]?.schema));
      plainSchema[node.id] = node;
      node.parent = parent;
    });
    set({ schema, plainSchema });
    // 
    runAsync(() => {
      updateExpressionContext();
      Object.values(plainSchema).forEach(resolveNodeDeps);
      // 
      Object.keys(plainSchema).forEach(id => {
        const node = plainSchema[id];
        if (node.events) {
          node.events.forEach(event => {
            addEvent(node.id, event, false);
          });
        }
      })
    });
  },
  setSchema: schema => {
    const plainSchema = {};
    travisSchema(schema, (node, parent) => {
      plainSchema[node.id] = node;
      node.parent = parent;
    })
    set({ schema, plainSchema });
  },
  updateNode: (id, newNode, isMerge: boolean = true) => set(state => {
    const { plainSchema, schema } = state;
    const oldNode = plainSchema[id];
    if (!oldNode) {
      console.error('schema node not found', id);
      return;
    }
    if (isMerge) {
      merge(oldNode, newNode);
      plainSchema[id] = oldNode;
    } else {
      plainSchema[id] = newNode;
    }
    resolveNodeDeps(plainSchema[id]);
    if (id === 'CanvasRoot') {
      merge(schema, newNode);
      plainSchema[id] = schema;
      return { plainSchema, schema };
    }
    return {
      plainSchema: { ...plainSchema },
      schema,
    }
  }),
  mountEvent: (id, event) => set(state => {
    const { plainSchema } = state;
    const schemaNode = plainSchema[id];
    const events = schemaNode.events || [];
    const eventIdx = events.findIndex(evt => evt.id === event.id);
    if (eventIdx !== -1) {
      events.splice(eventIdx, 1, event);
    } else {
      events.push(event);
    }
    schemaNode.events = events;
    return {
      plainSchema,
    };
  }),
  removeEvent: (id, eventId) => set(state => {
    const { plainSchema } = state;
    const schemaNode = plainSchema[id];
    const events = schemaNode.events;
    if (events?.length) {
      const eventIdx = events.findIndex(event => event.id === eventId);
      events.splice(eventIdx, 1);
    }
    return {
      plainSchema,
    };
  }),
  appendChild: (parentId, child, index?: number) => set(state => {
    const { plainSchema, schema } = state;
    const parent = plainSchema[parentId];
    if (!parent) {
      console.error('parent not found', parentId);
      return;
    }
    let { children } = parent;
    if (!children) {
      children = [];
    }
    if (isNil(index) || index >= children.length) {
      children.push(child);
    } else {
      children.splice(index, 0, child);
    }
    parent.children = children;

    plainSchema[child.id] = child;

    child.parent = parent;

    // 
    runAsync(() => {
      Object.values(plainSchema).forEach(resolveNodeDeps);
    })

    return { plainSchema: { ...plainSchema }, schema };
  }),
  moveChild: (id, targetParentId, slotType = '', idx = -1) => set(state => {
    const { plainSchema, schema } = state;
    const child = plainSchema[id];
    const targetParent = plainSchema[targetParentId];
    const sourceParent = child.parent;

    switch (slotType) {
      case SlotType.COMMON: 
        child.layout = { ...child.layout, inSlot: slotType };
        break;
      default:
        break;
    }

    sourceParent.children?.splice(sourceParent.children.findIndex(item => item === child), 1);
    const targetParentChildren = targetParent.children || [];
    if (idx === -1) {
      targetParentChildren.push(child);
    } else {
      targetParentChildren.splice(idx, 0, child);
    }
    targetParent.children = targetParentChildren;
    child.parent = targetParent;

    return { plainSchema: { ...plainSchema }, schema };
  }),
  deleteNode: (id) => set(state => {
    const { plainSchema, schema, deleteNode } = state;
    const node = plainSchema[id];

    if (node.children) {
      node.children.forEach(child => deleteNode(child.id));
    }


    // step 1 event
    const events = node.events;
    if (events) {
      const deleteEvent = useActionStore.getState().deleteEvent;
      events.map(event => event.id).forEach(eventId => {
        deleteEvent(id, eventId);
      })
    }

    // step 2 methods
    unmountMethods(id);

    // step 3 schema
    const parent = node.parent;
    const nodeIdxInChildren = parent.children.findIndex(child => child.id === id);
    parent.children.splice(nodeIdxInChildren, 1);
    delete plainSchema[id];

    // step 4  selectedhovered
    const { setSelected, setHovered } = useCanvasStore.getState();
    setSelected('');
    setHovered('');

    // step 5 
    Object.keys(deps).forEach(depKey => {
      const [nodeId] = depKey.split('.');
      if (nodeId === id) {
        delete deps[depKey];
      }
    })

    return {
      schema: schema,
      plainSchema: { ...plainSchema }
    };
  }),
}))

useSchemaStore.getState().setSchema(getInitialSchema());

Object.defineProperties(window, {
  '__SCHEMA__': {
    get() {
      return useSchemaStore.getState().schema;
    },
  },
  '__PLAIN_SCHEMA__': {
    get() {
      return useSchemaStore.getState().plainSchema;
    },
  },
});

export {
  useSchemaStore,
  travisSchema,
}