import React from 'react';
import { useDragLayer } from 'react-dnd';
import useComponentStore from '../components/store';
import { generateInitialState, getGridItemWidth } from '../misc';
import { DragSourceType, GRID_ROW_HEIGHT } from '../constant';
import { useStatesStore } from '../states';

const DargLayer = () => {
  const { isDragging, currentOffset, item, itemType, initialOffset } = useDragLayer((monitor) => ({
    isDragging: monitor.isDragging(),
    currentOffset: monitor.getSourceClientOffset(),
    item: monitor.getItem(),
    itemType: monitor.getItemType(),
    initialOffset: monitor.getInitialClientOffset(),
  }));

  const type = item?.type;

  const source = item?.source;

  const dragComponentInfo = useComponentStore(state => state[type]);
  const componentStates = useStatesStore(state => state[item?.id]);

  if (source !== DragSourceType.COMPONENT_PANEL) {
    return null;
  }

  const DragComponent = dragComponentInfo?.Component;

  if (!item || !DragComponent) {
    return null;
  }

  let previewProps = generateInitialState(dragComponentInfo?.schema);
  if (source === DragSourceType.CANVAS) {
    previewProps = { ...componentStates };
  }

  const { x, y } = currentOffset || { x: 0, y: 0 };
  const { layout } = item;

  const sizeStyle = {
    width: layout.width * getGridItemWidth(),
    height: layout.height * GRID_ROW_HEIGHT,
  };

  return (
    <div
      style={{
        position: 'fixed',
        left: x,
        top: y,
        pointerEvents: 'none',
        zIndex: 1000,
        cursor: 'grabbing',
        ...sizeStyle,
        opacity: '.5'
      }}
    >
      <DragComponent {...previewProps} />
    </div>
  );

}

export default DargLayer;