import { useState } from "react";
import useCanvasStore from "../canvas/store";
import { useEffect } from "react";
import evtManager from "../event";
import { EventType } from "../constant";
import { isInRect } from "../misc";
import { useDebugValue } from "react";


const useSchemaNode = (id) => {
  const [isDragOver, setIsDragOver] = useState(false);
  const inserting = useCanvasStore(state => state.inserting);
  const element: HTMLDivElement = useCanvasStore(state => state.containers[id]);

  useEffect(() => {
    if (!element) {
      return;
    }
    const handler = ({ item, coordi, clientOffset }) => {
      if (id === item.id) {
        // 
        return;
      }
      const inRect = isInRect(clientOffset, element.getBoundingClientRect());
      // id === 'Paper1' && console.log('setIsDragOver', id, inRect)
      setIsDragOver(inRect);
    }
    evtManager.on(EventType.DRAGGING, handler);
    return () => {
      evtManager.off(EventType.DRAGGING, handler);
    }

  }, [id, element]);

  useEffect(() => {
    if (!inserting) {
      setIsDragOver(false);
    }
  }, [inserting]);

  return { isDragOver };
}

export default useSchemaNode;