import React from "react";
import styles from './index.scss';
import { Popper as MUIPopper, Fade, Typography } from '@mui/material';
import CloseOutlined from '@mui/icons-material/CloseOutlined';
import { ReactNode } from "react";
import { useState } from "react";
import { useEffect } from "react";
import type { PopperPlacementType } from '@mui/material/Popper/BasePopper.types';
import { useMemo } from "react";
import { CSSProperties } from "react";
import { isNil, pick } from "lodash-es";

interface IProps {
  open: boolean;
  anchorEl?: HTMLElement;
  title?: ReactNode;
  showClose?: boolean;
  transition?: boolean;
  children?: ReactNode;
  placement?: PopperPlacementType;
  onOpenChange?: (open: boolean) => any;
  offset?: [offsetX: number, offsetY: number];
  width?: number;
  style?: CSSProperties;
  disablePadding?: boolean;
}

const Popper = (props: IProps) => {
  const { open, transition = true, title, showClose = true, anchorEl, placement, onOpenChange, children, offset = [0, 0], width, style = {}, disablePadding } = props;

  const containerStyle: CSSProperties = {
    ...style
  }

  if (!isNil(width)) {
    containerStyle.width = width;
  }
  if (disablePadding) {
    containerStyle.padding = 0;
  }

  return (
    <MUIPopper
      open={open}
      transition={transition}
      anchorEl={anchorEl}
      placement={placement}
      sx={{ zIndex: 20 }}
      modifiers={[
        {
          name: 'offset',
          options: {
            offset,
          }
        }
      ]}
    >
      {({ TransitionProps }) => (
        <Fade {...TransitionProps} timeout={200}>
          <div className={styles['popper-container']} style={containerStyle}>
            {(title || showClose) && (
              <div className={styles['popper-title']}>
                {title && <Typography variant="subtitle2" sx={{ color: 'var(--text-input-2)' }}>{title}</Typography>}
                {showClose && <span className={styles['popper-close-icon']} 
                  onClick={() => { 
                    onOpenChange?.(false);
                  }}>
                  <CloseOutlined fontSize="inherit" />
                </span>}
              </div>
            )}
            <div className={styles['popper-body']} style={pick(containerStyle, 'padding')}>
              {children}
            </div>
          </div>
        </Fade>
      )}
    </MUIPopper>
  )
}

export default Popper;